.list-translate {
  padding: 30px 0;

  @media (min-width: $mqsm) {
    padding: 67px 0 128px;

    background: $color_gray_light;
  }
}

.list-translate__wrapper {
  display: flex;
  flex-wrap: wrap;

  margin: 0 -10px;

  @media (min-width: $mqsm) {
    margin: -22px -15px;
  }
}

.list-translate__wrapper--mobile {
  @media (min-width: $mqsm) {
    display: none;
  }
}

.list-translate__wrapper--col {
  @media (min-width: $mqsm) {
    margin: auto;
  }

  @media (min-width: $mqmd) {
    margin: -30px 0 5px 0;
  }

  .list-translate__item {
    display: flex;
    align-items: flex-start;
    flex: 0 0 100%;
    flex-direction: column;

    @media (min-width: $mqxs) {
      flex-basis: 50%;
    }

    @media (min-width: $mqsm) {
      flex-basis: 100%;

      width: 350px;
      padding: 30px 0;
    }
  }
}

.list-translate__item {
  width: 100%;
  padding: 10px;

  @media (min-width: $mqxs) {
    width: 50%;
  }

  @media (min-width: $mqsm) {
    width: calc( 100% / 3 );
    padding: 22px 15px;
  }
}

.list-translate__item--main {
  position: relative;

  .list-translate__content {
    flex-direction: row;

    background-color: $color_white;

    &:first-child {
      width: 80%;
    }
  }

  .list-translate__icon-wrap {
    position: absolute;
    top: 30%;
    right: 10%;
  }

  .list-translate__shape {
    margin: 0;
  }
}

.list-translate__content {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: space-between;

  margin: 10px 0;

  @media (min-width: $mqsm) {
    flex-direction: row;
  }
}

.list-translate__image-wrap {
  flex: 1 1 auto;

  img {
    display: block;

    width: 100%;
  }
}

.list-translate__control {
  display: flex;
  align-self: flex-end;
  flex: 0 0 95px;
  flex-direction: column;
  justify-content: space-between;
}

.list-translate__icon-wrap {
  display: flex;
  align-items: center;
  flex: 0 0 24px;
  flex-direction: column;

  max-width: 24px;
  margin: -10px 0 0 12px;
}

.list-translate__icon {
  padding: 10px;

  .icon {
    width: 24px;
  }
}

.list-translate__shape {
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 0 0 16px 0;
  padding: 6px 10px;

  border: 3px solid $color_gray;
  background: $color_white;

  @media (min-width: $mqsm) {
    margin: 10px 0 0 0;
    padding: 8px 16px;

    border: 0;
  }
}

.list-translate__mobile-top {
  display: flex;
  align-items: center;

  width: 100%;
  margin: 0 0 10px 0;
}

.list-translate__mobile-bottom {
  display: flex;
  align-items: flex-start;

  width: 100%;
}

.list-translate__mobile-item {
  &:first-child {
    flex: 1 1 242px;

    margin: 0 16px 0 0;
  }

  &:last-child {
    flex: 0 0 70px;

    text-align: center;

    .icon {
      width: 14px;
    }
  }
}

.list-translate__langs {
  padding: 7px 10px;

  color: $color_main;
  font-size: 10px;
  font-weight: 700;
  text-align: center;
  white-space: nowrap;
  text-transform: uppercase;

  border-radius: 10px;
  background: $color_gray;
}
