.translation-block {
  display: inline-block;

  margin: 0 3% 3% 0;

  @media (min-width: $mqmd) {
    width: 46%;
  }

  p {
    margin: 0;

    font-size: 15px;
  }

  img {
    display: block;
  }

  .btn {
    font-size: 12px;
  }
}

.translation-block__image {
  display: flex;

  background-color: $color_white;
}

.translation-block__word {
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 10px 0;
}

.translation-block__controls {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 40%;

  text-align: center;

  @media (min-width: $mqsm) {
    justify-content: flex-end;
  }

  img {
    max-width: 60px;
    margin: 0 auto;
  }
}

.translation-block__language {
  position: relative;
  top: 5px;
  right: 5px;

  align-self: flex-end;

  width: 40px;
  height: 40px;
  padding-top: 8px;

  text-align: center;

  border-radius: 50%;
  background-color: $color_main;

  @media (min-width: $mqsm) {
    display: none;
  }

  .icon {
    width: 17px;

    color: $color_white;
  }
}
