// box sizing
html {
  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

img {
  box-sizing: content-box;
}

// nastavení velikosti písma
:root {
  font-size: 100%;
}

// základní definice
body {
  position: relative;

  display: flex;
  flex-direction: column;

  height: 100%;
  margin: 0;

  color: $color_secondary;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 1;

  background-color: $color_white;

  @media (min-width: $mqsm) {
    font-size: 16px;
  }
}

// základní nastavení respo
@-ms-viewport {
  width: device-width;
}

img,
svg {
  display: inline-block;

  max-width: 100%;
  height: auto;

  border: none;
}

iframe {
  max-width: 100%;
}

video {
  outline: 0;
}
