.nav {
  z-index: $index_menu;

  ul {
    display: flex;
    align-items: center;

    margin: 0;
    padding: 0;

    list-style-type: none;
  }

  li {
    display: inline-block;

    padding: 0;
  }

  a {
    display: inline-block;

    padding: 10px;

    color: $color_white;
    font-size: 14px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
      color: $color_secondary;

      .nav__icon {
        color: $color_white;
      }
    }

    &.nav__control {
      padding-right: 8px;
      padding-left: 8px;
    }

    // aktivní položka menu
    &.is-active {
      color: $color_secondary;
    }
  }
}

.nav--header {
  display: flex;
  align-items: center;
}

.nav--footer {
  z-index: $index_base;

  display: none;

  ul {
    @media (min-width: $mqsm) {
      margin: 0 -10px 0 0;
    }
  }

  a {
    margin: -10px 0;

    &:hover {
      color: $color_main;
    }
  }

  @media (min-width: $mqsm) {
    display: block;
  }
}

.nav__icon {
  width: 24px;

  transition: all 200ms ease;
}

.nav__desktop {
  display: none;

  @media (min-width: $mq_menu) {
    display: block;
  }
}

.nav__mobile {
  display: flex;
  align-items: center;

  .icon {
    width: 24px;

    color: $color_white;
  }

  @media (min-width: $mq_menu) {
    display: none;
  }
}

.nav__mobile-search {
  padding: 10px;

  cursor: pointer;
}
