.table {
  width: 100%;
  margin: 0 0 30px 0;

  border-collapse: collapse;

  color: $color_secondary;
  font-size: 15px;

  @media (min-width: $mqsm) {
    table-layout: fixed;
  }

  strong {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
  }

  th,
  td {
    padding: 10px;

    line-height: 1.2;
    text-align: left;

    border: 1px solid $color_gray_dark;
  }

  th {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    text-align: center;

    border: 1px solid transparent;
    border-bottom: 1px solid $color_gray_dark;
  }

  tr {
    &:hover td {
      background-color: $color_gray_light; // zvýraznění řádku po najetí myší
    }
  }
}

.table--limit {
  max-width: 475px;
}

.table--no-margin {
  margin: 0;
}

.table + hr {
  margin: 35px 0 29px;

  @media (min-width: $mqsm) {
    margin: 59px 0 53px;
  }
}

// obalový element (pro respo)
.table__wrapper {
  overflow-x: auto;

  max-width: 100%;
}
