.search-top {
  display: block;

  padding-top: 30px;

  background: $color_gray;

  @media (max-width: $mqsm - 1px) {
    display: none;
  }

  .search__wrapper {
    margin-bottom: 0;
    padding: 0 0 20px 0;

    @media (min-width: $mqmd) {
      padding: 41px 0;
    }
  }

  .search-alt__wrapper {
    margin-top: 0;
    margin-bottom: 0;
    padding: 0 0 20px 0;

    border-top: 0;
    border-bottom: 0;

    @media (min-width: $mqlg) {
      padding: 21px 0 11px 0;
    }
  }

  &.is-open {
    @media (max-width: $mqsm - 1px) {
      display: none;
    }

    .icon {
      transform: scale( 1, 1 );
    }
  }
}

.search-top__wrapper {
  position: relative;

  max-width: $mqlg;
  margin: 0 auto;
}

.search-top__close {
  position: absolute;
  right: 30px;
  bottom: -10px;
  z-index: $index_base;

  cursor: pointer;

  &:before {
    content: '';

    position: absolute;
    top: 50%;
    left: 50%;

    width: 40px;
    height: 40px;

    background: $color_gray_light;

    transform: translate( -50%, -50% ) rotate( -45deg );
  }

  .icon {
    width: 18px;

    color: $color_gray_dark;

    transition: all 300ms ease;
    transform: scale( -1, -1 );
  }
}

.search-top__close--alt {
  &:before {
    background: $color_white;
  }
}
