// základní nastavení typografie
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// nadpisy
h1,
h2,
h3,
h4 {
  margin-top: 0;

  color: $color_secondary;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.3;
}

h1 {
  font-size: 24px;
  font-weight: 900;

  @media (min-width: $mqsm) {
    font-size: 36px;
    text-transform: uppercase;
  }
}

h2 {
  font-size: 20px;
  font-weight: 900;

  @media (min-width: $mqsm) {
    font-size: 18px;
  }
}

h3 {
  font-size: 18px;
}

h4 {
  font-size: 16px;
}

// odkazy
a {
  color: $color_main;

  transition: all 200ms ease;


  @media (max-width: 767px) {
    &:visited {
      color: #afafaf;
    }
  }

  &:hover,
  &:focus {
    color: $color_tertiary;
  }
}

// odstavce
p {
  margin-top: 0;

  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 1.4;

  @media (min-width: $mqsm) {
    font-family: 'Roboto', sans-serif;
    font-size: 17px;
  }

  &.lead {
    color: $color_black;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.43;

    @media (min-width: $mqsm) {
      font-size: 20px;
      line-height: 1.2;
    }
  }
}

// seznamy
ul,
ol {
  margin: 0 0 20px 0;
  padding: 0 0 0 18px;

  line-height: 1.3;

  li {
    padding: 3px 3px 3px 0;
  }

  ul,
  ol {
    margin: 0;
    padding: 3px 3px 0 10px;

    li {
      &:last-child {
        padding-bottom: 0;
      }
    }
  }
}

hr {
  width: 100%;
  height: 1px;
  margin: 25px 0;

  border: 0;
  background: url( '../img/hr.svg' ) repeat-x left;
  background-size: cover;

  @media (min-width: $mqsm) {
    margin: 45px 0;
  }
}

p + hr {
  margin: 35px 0;

  @media (min-width: $mqsm) {
    margin: 67px 0;
  }
}
