// barvy

  // základní barvy
  $color_white: #fff;
  $color_black: #000;

  // šedá barva
  $color_gray: #e4ebee;
  $color_gray_dark: #abb1c2;
  $color_gray_light: #f2f6f8;

  // hlavní barvy projektu
  $color_main: #03c9a9;
  $color_main_dark: darken( $color_main, 10% );
  $color_main_light: lighten( $color_main, 10% );

  // sekundární barvy projektu
  $color_secondary: #1c2641;
  $color_secondary_dark: darken( $color_secondary, 10% );
  $color_secondary_light: lighten( $color_secondary, 10% );

  // barvy pro chybové hlášky
  $color_success: #5cb85c;
  $color_info: $color_main;
  $color_danger: #ff5d5d;

  // další
  $color_tertiary: #0366c9;
  $color_tertiary_dark: darken( $color_tertiary, 10% );
  $color_tertiary_light: lighten( $color_secondary, 10% );

  // lightbox
  $sl-overlay-background: $color_main;
  $sl-overlay-opacity: 0.9;
  $sl-navigation-color: $color_white;

// zakulacené rohy
$radius: 20px;
$radius_button: 6px;

// layout
$layout_gap: 10px;

// breakpointy

  // základní převzaty z Bootstrap Gridu
  $mqxs: 576px;     // small
  $mqsm: 768px;     // medium
  $mqmd: 992px;     // large
  $mqlg: 1130px;    // extra large
  // rozšířené
  $mqxxs: 419px;    // extra small
  $mqxlg: 1420px;   // max large
  $mq_menu: 992px;  // respo menu
  $mq_modal: 700px; // modal okno

// z-index
$index_base: 1;
$index_page: 10;
$index_menu: 100;
$index_modal: 1000;
