.hand {
  h2 {
    margin: 0 0 7px 0;

    color: $color_secondary;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 700;
  }
}

.hand__wrap {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;

  margin: -5px -5px 27px -5px;
}

.hand__main {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 95px;
  height: 95px;
  margin: 5px 45px 5px 5px;

  border-radius: 6px;
  background: $color_main;

  .icon {
    width: 24px;

    color: $color_white;
  }
}

.hand__item {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 64px;
  height: 64px;
  margin: 5px;

  border-radius: 6px;
  background: $color_white;

  .icon {
    width: 17px;

    color: $color_secondary;
  }
}

.hand__item--danger {
  background: $color_danger;

  .icon {
    width: 26px;

    color: $color_white;
  }
}

.hand__box {
  padding: 25px 30px 15px 30px;

  border-radius: 10px;
  background: $color_gray_light;
}
