.translation {
  background: $color_gray;

  p {
    max-width: 570px;
  }
}

.translation__wrapper {
  max-width: 730px;
  padding: 27px 0;

  @media (min-width: $mqsm) {
    padding: 40px 0;
  }
}

.translation__count-desktop {
  display: none;

  @media (min-width: $mqsm) {
    display: block;
  }
}

.translation__count-mobile {
  @media (min-width: $mqsm) {
    display: none;
  }
}
