.layout-aside {
  &:first-child {
    position: relative;

    padding: 30px $layout_gap;

    background: $color_gray_light;

    &:before {
      content: '';

      position: absolute;
      top: 0;
      right: 0;
      z-index: -1;

      width: 100vw;
      height: 100%;

      background: $color_gray_light;
    }

    @media (min-width: $mqmd) {
      max-width: 390px;
      padding: 88px 63px 70px $layout_gap;
    }
  }

  &:last-child {
    padding: 30px $layout_gap;

    @media (min-width: $mqmd) {
      padding: 78px $layout_gap 70px 63px;
    }
  }
}
