.nav-lang {
  position: relative;

  display: flex;
  display: none;
  align-items: center;

  padding: 10px 0 10px 8px;

  cursor: pointer;
  user-select: none;

  @media (min-width: $mq_menu) {
    display: flex;
  }

  &:hover,
  &.is-open {
    display: flex;

    .nav-lang__dropdown {
      color: $color_white;
    }

    .nav-lang__selected,
    .nav__icon {
      color: $color_white;
    }
  }

  &.is-open {
    .nav-lang__dropdown {
      transform: scale( -1 );
    }

    .nav-lang__choose {
      display: block;
    }
  }
}

.nav-lang__dropdown {
  width: 12px;

  transition: all 200ms ease;
}

.nav-lang__selected {
  padding: 0 4px 0 5px;

  color: $color_secondary;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;

  transition: all 200ms ease;
}

.nav-lang__choose {
  position: absolute;
  top: calc( 100% + 10px );
  right: 0;

  display: none;

  min-width: 225px;

  background: $color_secondary;

  cursor: default;

  &:before,
  &:after {
    content: '';

    position: absolute;
    right: 40px;
    bottom: 100%;

    width: 0;
    height: 0;

    border: solid transparent;

    pointer-events: none;
  }

  &:before {
    margin-left: -7px;

    border-width: 7px;
    border-color: rgba( 255, 255, 255, 0 );
    border-bottom-color: $color_secondary;
  }

  &:after {
    margin-left: -7px;

    border-width: 7px;
    border-color: rgba( 255, 255, 255, 0 );
    border-bottom-color: $color_secondary;
  }

  ul {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    justify-content: flex-end;

    margin: 0;
    padding: 10px 0;

    list-style: none;
  }

  li {
    display: block;

    margin: 0;
    padding: 0;
  }

  a {
    display: inline-block;

    text-align: right;

    &:hover {
      color: $color_main;
    }
  }
}
