.recent {
  padding: 19px 0;

  background: $color_white;

  @media (min-width: $mqsm) {
    margin: -10px 0 0 0;
    padding: 0 0 70px 0;

    background: $color_gray;
  }

  ul {
    margin: 0;
    padding: 0 0 0 16px;

    list-style: none;

    @media (min-width: $mqsm) {
      padding: 0;
    }
  }

  li {
    display: block;
  }

  a {
    display: inline-block;

    color: $color_secondary;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
    text-decoration: none;

    @media (min-width: $mqsm) {
      color: $color_gray_dark;
      font-weight: 400;
    }

    &:hover {
      color: $color_main;
    }
  }
}

.recent__row {
  position: relative;

  display: flex;
  flex-direction: column;

  margin: 0 (-$layout_gap);

  @media (min-width: $mqsm) {
    flex-direction: row;
  }
}

.recent__item {
  padding: $layout_gap;

  @media (min-width: $mqsm) {
    flex: 0 1 227px;
  }
}

.recent__headline {
  position: relative;
  z-index: $index_base;

  display: inline-block;

  margin-bottom: 10px;
  padding: 8px 12px 8px 0;

  color: $color_secondary;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;

  &:before {
    content: '';

    position: absolute;
    top: -1px;
    right: 0;
    z-index: -1;

    width: 100vw;
    height: 100%;

    border-radius: 0 10px 10px 0;
    background: $color_gray;
  }

  @media (min-width: $mqsm) {
    margin-bottom: 15px;

    color: $color_gray_dark;
    font-size: 16px;
    font-weight: 600;

    &:before {
      display: none;
    }
  }
}
