.checkbox {
  display: inline-block;

  margin: 19px 0 0 0;
  padding: 0 0 0 36px;

  color: $color_secondary;
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.25;
  vertical-align: middle;
  text-indent: -36px;

  user-select: none;

  input[type='checkbox'] {
    display: none;
  }

  span {
    position: relative;

    display: inline-block;

    width: 24px;
    height: 24px;
    margin: 0 12px 0 0;

    vertical-align: middle;

    border-radius: 5px;
    background: $color_white;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 16px 13px;
  }

  :checked + span {
    background-image: url( '../img/check.svg' );
  }
}

.checkbox--light {
  color: $color_white;
}
