@keyframes showRight {
  0% {
    opacity: 0;
    transform: translate( -50px, 0 );
  }

  100% {
    opacity: 1;
    transform: translate( 0, 0 );
  }
}

@keyframes showLeft {
  0% {
    opacity: 0;
    transform: translate( 50px, 0 );
  }

  100% {
    opacity: 1;
    transform: translate( 0, 0 );
  }
}

@keyframes showUp {
  0% {
    opacity: 0;
    transform: translate( 0, 50px );
  }

  100% {
    opacity: 1;
    transform: translate( 0, 0 );
  }
}

@keyframes showDown {
  0% {
    opacity: 0;
    transform: translate( 0, -50px );
  }

  100% {
    opacity: 1;
    transform: translate( 0, 0 );
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes showMenu {
  0% {
    transform: translate( 100%, 0 );
  }

  100% {
    transform: translate( 0, 0 );
  }
}
