.dropdown {
  flex: 1 1 100%;

  margin: 20px 0;
}

.dropdown__item {
  width: 100%;
  margin-bottom: 10px;

  border-radius: $radius;
  background: $color_gray;

  &:last-child {
    margin: 0;
  }

  &.is-open {
    .dropdown__item__name {
      &:after {
        transform: translate( 0, -50% ) scale( -1, -1 );
      }
    }

    .dropdown__item__detail {
      display: block;
    }
  }
}

.dropdown__item__name {
  position: relative;

  padding: 12px 40px 12px 20px;

  color: $color_secondary;
  font-size: 17px;
  font-weight: 700;

  cursor: pointer;

  &:after {
    content: '';

    position: absolute;
    top: 50%;
    right: 23px;

    width: 12px;
    height: 8px;

    background: url( '../img/arrow-down.svg' ) no-repeat center;
    background-size: cover;

    transition: all 300ms ease;
    transform: translate( 0, -50% ) scale( 1, 1 );
  }
}

.dropdown__item__detail {
  position: relative;

  display: none;

  margin: 0 73px 0 20px;
  padding: 15px 0 18px 0;

  &:before {
    content: '';

    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 1px;

    background: $color_secondary;
  }
}
