.mobile-text {
  p {
    color: $color_black;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 500;
  }

  a {
    color: $color_main;
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
    font-weight: 700;
    text-decoration: underline;
    text-transform: uppercase;
  }
}
