// základní vzhled input polí
input,
textarea,
select {
  width: 100%;
  padding: 10px;

  color: $color_secondary;
  font-size: 12px;
  font-weight: 400;

  border: 0 solid transparent;
  border-radius: 0;
  background: $color_gray;

  caret-color: $color_main;

  @supports (-webkit-touch-callout: none) {
    font-size: 16px;
  }

  @media (min-width: $mqsm) {
    width: 100%;
    padding: 20px 30px;

    font-size: 18px;
    font-weight: 700;
    line-height: 18px; // pro sladění výšky

    border: 0 solid transparent;
    border-radius: $radius;
    background: $color_white;
    box-shadow: 0 0 4px rgba( 0, 0, 0, 0.1 );
  }

  &:focus {
    border-color: $color_main_dark;
    outline: 0;
  }

  &:placeholder {
    color: #afafaf;

    opacity: 1;
  }

  &.is-success {
    border-color: $color_success;
  }

  &.is-danger {
    border-color: $color_danger;
  }
}

// úpravy pro textarea
textarea {
  line-height: 1.4; // čitelnější rozestupy mezi řádky
}

// popisek pole
label {
  display: block;

  margin: 0 0 17px 0;

  color: $color_secondary;
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  font-weight: 900;

  transition: all 300ms ease;
}

// ošetření submit inputu, pokud je použit
button,
input[type='submit'] {
  width: auto;

  cursor: pointer;
}

// ošetření chekboxu a radio buttonu
input[type='checkbox'],
input[type='radio'] {
  width: auto;
}
