.layout-sign {
  &:first-child {
    padding: 30px $layout_gap;

    @media (min-width: $mqmd) {
      padding: 59px $layout_gap 70px;
    }
  }

  &:last-child {
    position: relative;

    padding: 30px $layout_gap;

    background: $color_gray_light;

    &:before {
      content: '';

      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;

      width: 100vw;
      height: 100%;

      background: $color_gray_light;
    }

    @media (min-width: $mqmd) {
      max-width: 455px;
      padding: 66px $layout_gap 70px 95px;
    }
  }
}
