.mobile-action-bar {
  position: fixed;
  top: 67px;
  left: 0;
  z-index: $index_menu;

  width: 100%;

  background: $color_gray;

  @media (min-width: $mqsm) {
    display: none;
  }

  a {
    color: $color_main;
    font-size: 12px;
    font-weight: 700;
    text-decoration: underline;
  }
}

.mobile-action-bar__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;

  height: 48px;
}

.mobile-action-bar__content {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
}

.mobile-action-bar__item {
  display: flex;
  align-items: center;
}


.mobile-action-bar__back {
  margin: -10px 0 -10px -10px;
  padding: 10px;

  cursor: pointer;

  .icon {
    width: 24px;
  }
}

.mobile-action-bar__icon {
  margin: -10px -10px -10px 14px;
  padding: 10px;

  .icon {
    width: 24px;
  }
}
