.link {
  display: inline-block;

  color: $color_tertiary;
  font-size: 17px;
  font-weight: 700;

  &:hover {
    color: $color_tertiary_dark;
  }
}

.link--no-underline {
  font-size: 14px;
  text-decoration: none;
}

.link--light {
  font-weight: 400;
}

.link--space {
  margin: 0 0 26px 0;
}
