.mobile-header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;

  width: 100%;
  margin: 0 0 10px 0;

  h1 {
    margin: 0 10px 0 0;

    font-size: 20px;
  }

  a {
    color: $color_secondary;
    text-decoration: underline $color_secondary;

    &:hover {
      color: $color_main;
      text-decoration: underline $color_main;
    }
  }

  @media (min-width: $mqsm) {
    display: none;
  }
}

.mobile-header__icon-wrap {
  display: flex;
}

.mobile-header__icon {
  padding: 7px;
}
