.info {
  position: relative;

  padding: 54px 20px 20px 20px;

  border-radius: $radius;
  background: $color_gray;

  @media (min-width: $mqmd) {
    padding: 42px 65px;
  }

  p {
    font-size: 14px;

    &:last-child {
      margin: 0;
    }
  }
}

.info__icon {
  position: absolute;
  top: 20px;
  left: 20px;

  width: 24px;

  color: $color_main;
}
