.mobile-search {
  padding: 16px 0;

  background: $color_secondary;

  &--modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: $index_modal;

    display: none;
    overflow: auto;

    width: 100%;
    height: 100%;

    background: $color_white;

    animation: 300ms showMenu;
  }

  &--top {
    position: fixed;
    top: 0;
    left: 0;
    z-index: $index_modal;

    width: 100%;
    padding-bottom: 0;

    background: $color_white;

    @media (min-width: $mqsm) {
      display: none;
    }
  }

  @media (min-width: $mqsm) {
    display: none;
  }
}

.mobile-search__wrapper {
  display: flex;
  align-items: stretch;
  flex: 1 1 auto;
  justify-content: space-between;
}

.mobile-search__select {
  position: relative;

  &:first-child {
    .mobile-search__selected {
      padding-right: 30px;

      border-radius: 6px 0 0 6px;

      &:after {
        right: 10px;
      }
    }
  }

  &:last-child {
    .mobile-search__selected {
      padding-left: 30px;

      border-radius: 0 6px 6px 0;

      &:after {
        left: 10px;
      }
    }

    .mobile-search__dropdown {
      right: 0;
      left: auto;

      text-align: right;
    }
  }

  &.is-open {
    &:first-child {
      .mobile-search__selected {
        border-bottom-left-radius: 0;
      }
    }

    &:last-child {
      .mobile-search__selected {
        border-bottom-right-radius: 0;
      }
    }

    .mobile-search__selected {
      color: $color_white;

      background: $color_secondary;

      &:after {
        border-top: 0;
        border-bottom: 7px solid $color_white;
      }
    }

    .mobile-search__dropdown {
      display: block;
    }
  }

  &--home {
    &.is-open {
      .mobile-search__selected {
        background: $color_main-dark;
      }
    }
  }
}

.mobile-search__selected {
  position: relative;

  display: flex;
  align-items: center;

  min-width: 70px;
  height: 100%;
  padding: 10px;

  color: $color_white;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;

  background: $color_main;

  cursor: pointer;

  &:after {
    content: '';

    position: absolute;
    top: 50%;

    width: 0;
    height: 0;

    border-top: 7px solid $color_secondary;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;

    transform: translate( 0, -50% );
  }
}

.mobile-search__dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: $index_menu;

  display: none;

  min-width: 218px;
  padding: 10px 16px;

  background: $color_secondary;

  ul {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  li {
    padding: 7px 0;

    color: $color_white;
    font-size: 18px;
    font-weight: 700;
    text-transform: none;

    cursor: pointer;
    transition: all 300ms ease;

    &:hover {
      color: $color_main;
    }
  }
}

.mobile-search__option {
  margin: 24px 0;

  border-top: 1px solid $color_main;

  .checkbox {
    margin: 24px 0 0 0;
  }
}

.mobile-search__top {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;
  padding: 0 0 16px 0;

  &:after {
    content: '';

    position: absolute;
    bottom: 0;
    left: 50%;

    width: 100vw;
    height: 2px;

    background: $color_white;
    box-shadow: 0 2px 2px rgba( 0, 0, 0, 0.16 );

    transform: translate( -50%, 0 );
  }
}

.mobile-search__back {
  margin: -10px 6px -10px -10px;
  padding: 10px;

  cursor: pointer;

  .icon {
    width: 24px;
  }
}

.mobile-search__content {
  padding: 29px 0 0 0;

  ul {
    margin: 0;
    padding: 0 0 0 16px;

    list-style: none;
  }

  li {
    display: block;

    margin: 0;
    padding: 0;
  }

  a {
    display: inline-block;

    color: $color_secondary;
    font-size: 18px;
    font-weight: 700;
    line-height: 2;
    text-decoration: none;

    &:hover {
      color: $color_main;
    }
  }
}

.mobile-search__input-wrap {
  position: relative;

  flex: 1 1 auto;

  input {
    position: relative;
    z-index: ($index_page + 1);

    align-self: stretch;

    height: 100%;
  }

  &.js-active {
    .mobile-search__input-helper {
      display: block;
    }
  }
}

.mobile-search__input-helper {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: $index_page;

  display: none;

  width: 100%;
  margin: -1px 0 0 0;
  padding: 16px;

  background: $color_gray;
  box-shadow: 0 0 4px rgba( 0, 0, 0, 0.16 );

  ul {
    display: flex;
    flex-direction: column;

    margin: 0;
    padding: 0;

    list-style: none;
  }

  li {
    display: inline-block;

    margin: 0;
    padding: 0;

    color: $color_secondary;
    font-size: 18px;
    font-weight: 700;
    line-height: 2;
    text-decoration: none;

    cursor: pointer;
    transition: all 300ms ease;

    &:hover {
      color: $color_main;
    }
  }
}
