.special-info {
  display: inline-block;

  margin: 10px 0;
  padding: 16px 42px;

  color: $color_main;
  font-size: 18px;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;

  border: 5px solid $color_main;
  border-radius: 20px;

  strong {
    font-weight: 700;
  }
}
