.layout-column {
  &:first-child {
    padding: 30px ( $layout_gap + 5px );

    @media (min-width: $mqsm) {
      padding: 30px ( $layout_gap );
    }

    @media (min-width: $mqmd) {
      padding: 33px 80px 70px $layout_gap;
    }
  }

  &:last-child {
    position: relative;

    padding: 30px ( $layout_gap + 5px );

    @media (min-width: $mqsm) {
      padding: 30px ( $layout_gap );

      background: $color_gray_light;

      &:before {
        content: '';

        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;

        width: 100vw;
        height: 100%;

        background: $color_gray_light;
      }
    }

    @media (min-width: $mqmd) {
      padding: 173px $layout_gap 70px 80px;
    }
  }
}
