.detail-block {
  width: 100%;
  margin: 0 0 25px 0;

  p {
    &:last-child,
    &:last-of-type {
      margin: 0;
    }
  }

  & + hr {
    margin-top: 0;
  }

  @media (min-width: $mqsm) {
    margin: 0 0 58px 0;
  }
}

.detail-block__headline,
.detail-block__name {
  margin: 0 0 7px 0;
}

.detail-block__lead {
  margin: 0 0 36px 0;
}
