.search-alt {
  display: none;

  padding: 20px 0 39px;

  background: $color_gray;

  @media (min-width: $mqsm) {
    display: block;
  }
}

.search-alt__wrapper {
  padding: 65px 0;

  border-top: 3px solid $color_main;
  border-bottom: 1px solid $color_gray_dark;
}

.search-alt__wrap {
  max-width: 730px;
  margin: auto;
}

.search-alt__top {
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin: -15px;

  .search__translate__item {
    margin-right: 30px;
  }
}

.search-alt__options {
  display: flex;

  .checkbox {
    margin-right: 38px;

    &:last-child {
      margin-right: 0;
    }
  }
}
