.tabs {
  width: 100%;
}

.tabs--sign {
  @media (min-width: $mqsm) {
    width: calc( 100% + #{$layout_gap} );
  }
}

.tabs__nav {
  display: flex;
  align-items: center;

  width: 100%;
  margin: 0;
  padding: 0;

  list-style: none;

  li {
    display: inline-block;

    margin: 0 10px 0 0;
    padding: 8px 16px;

    color: $color_white;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;

    border-radius: 6px 6px 0 0;
    background: $color_main;

    cursor: pointer;

    &.tabs__options {
      display: none;

      margin: 0 14px 0 auto;

      color: $color_secondary;
      font-family: 'Roboto', sans-serif;
      font-size: 12px;
      text-transform: none;

      border-radius: 0;
      background: transparent;

      @media (min-width: $mqmd) {
        display: block;
      }
    }

    &.is-open {
      color: $color_main;

      background: $color_gray;
    }
  }
}

.tabs__tab {
  position: relative;

  display: none;

  padding: 20px 10px;

  background: $color_gray;

  @media (min-width: $mqmd) {
    padding: 33px 66px 49px 40px;

    &:before,
    &:after {
      content: '';

      position: absolute;
      bottom: 37px;
      left: 100%;
      z-index: $index_base;

      width: 0;
      height: 0;

      border: solid transparent;

      pointer-events: none;
    }

    &:before {
      margin-right: -30px;

      border-width: 30px;
      border-color: rgba( 255, 255, 255, 0 );
      border-left-color: $color_gray;
    }

    &:after {
      margin-right: -30px;

      border-width: 30px;
      border-color: rgba( 255, 255, 255, 0 );
      border-left-color: $color_gray;
    }
  }
}
