.naming {
  position: relative;

  display: inline-block;

  margin: 0 0 16px 0;
  padding: 8px 11px 6px 0;

  color: $color_secondary;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 700;

  &:before {
    content: '';

    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;

    width: 100vw;
    height: 100%;

    border-radius: 0 10px 10px 0;
    background: $color_gray;
  }


  @media (min-width: $mqsm) {
    margin: 0 0 9px 0;
    padding: 0;

    color: $color_secondary;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;

    &:before {
      display: none;
    }
  }

  .dropdown__item & {
    &:before {
      display: none;
    }
  }
}
