.video {
  position: relative;

  display: flex;

  margin-bottom: 5px;

  video {
    display: block;
  }
}

.video--small {
  .video__content {
    flex: 1 1 80%;
  }

  .video__controls {
    flex: 1 1 20%;
  }

  .video__link {
    top: 5px;
    right: 5px;

    width: 30px;
    height: 30px;

    .icon {
      width: 15px;
      padding-top: 14px;
    }
  }
}

.video--shrink {
  width: 80%;
}

.video--fixed {
  .video__content {
    flex: 0 1 auto;
  }

  .video__controls {
    flex-grow: 0;
  }
}

.video__content {
  flex: 1 1 85%;

  background-color: $color_white;
}

.video__controls {
  position: relative;

  display: flex;
  align-items: center;
  flex: 1 1 15%;
  flex-direction: column;
  justify-content: flex-end;

  background-color: $color_white;
}

.video__link {
  position: absolute;
  top: 10px;
  right: 10px;

  width: 36px;
  height: 36px;

  color: $color_white;
  text-align: center;
  text-decoration: none;

  border-radius: 50%;
  background-color: $color_main;

  @media (min-width: $mqsm) {
    display: none;
  }

  &:hover,
  &:focus {
    background-color: $color_secondary;
  }

  .icon {
    width: 16px;
    padding-top: 20px;

    color: $color_white;
  }
}

.video__sign {
  padding: 10px;

  background-color: $color_white;
}

.video__down {
  position: absolute;
  right: -15%;
  bottom: 10%;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  .icon {
    width: 24px;
  }
}

.video__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
