.search {
  display: none;

  background: $color_gray;

  @media (min-width: $mqsm) {
    display: block;
  }
}

.search__wrapper {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: space-between;

  margin: -15px -15px 0 -15px;
}

.search__input {
  position: relative;

  input {
    padding-right: 70px;
  }

  button {
    position: absolute;
    top: 50%;
    right: 8px;

    transform: translate( 0, -50% );
  }
}

.search__item {
  position: relative;

  display: flex;
  flex: 1 0 auto;
  flex-direction: column;

  width: calc( 100% / 3 );
  padding: 15px;

  &.is-active {
    label {
      color: $color_main;
    }
  }
}

.search__switch {
  position: absolute;
  right: -16px;
  bottom: 28px;
  z-index: $index_base;

  padding: 7px;

  cursor: pointer;
  transform: rotate( 90deg );

  .icon {
    width: 16px;
  }
}
