.section {
  padding: 27px 0;

  @media (min-width: $mqsm) {
    padding: 40px 0 70px 0;
  }
}

.section--columns {
  overflow: hidden; // z důvodu pozadí
}

.section--no-space {
  padding: 0;
}

.section--back {
  background-color: $color_gray_light;
}

.section__wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
