.footer {
  flex-shrink: 0;

  padding: 14px 0 10px 0;

  color: $color_secondary;

  background: $color_gray;

  @media (min-width: $mqsm) {
    padding: 13px 0;

    color: $color_white;

    background: $color_secondary;
  }
}

.footer__wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer__copy {
  margin: 0;

  color: $color_secondary;
  font-size: 9px;

  @media (min-width: $mqsm) {
    color: $color_gray;
    font-size: 12px;
  }

  span {
    color: $color_main;

    @media (min-width: $mqsm) {
      color: $color_gray;
    }
  }
}
