.detail-word {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
}

.detail-word__headline {
  margin: 0 10px 3px 0;
}

.detail-word__links {
  margin: 0 0 6px 5px;
  padding: 0;

  list-style: none;

  li {
    position: relative;

    display: inline-block;

    margin: 0;
    padding: 0;

    &:before {
      content: '/ ';

      position: absolute;
      top: 50%;
      left: -5px;

      color: $color_tertiary;
      font-size: 17px;
      font-weight: 700;

      transform: translate( 0, -50% );
    }
  }

  a {
    padding: 5px;
  }
}
