.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;

  display: none;

  width: calc( 100% - #{$layout_gap} - 5px );
  height: 100%;

  background: $color_secondary;

  animation: showMenu 300ms;
}

.mobile-menu__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  height: 100%;
}

.mobile-menu__scroll {
  display: flex;
  overflow: auto;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: space-between;

  height: 100%;
  padding: 0 16px;
}

.mobile-menu__top {
  margin: 0 16px;
  padding: 20px 80px 20px 0;

  border-bottom: 1px solid $color_main;

  .icon {
    width: 24px;

    color: $color_white;
  }
}

.mobile-menu__bottom {
  padding: 30px 0 63px;

  text-align: right;

  border-top: 1px solid $color_main;
}

.mobile-menu__nav {
  flex: 1 1 auto;

  padding: 26px 0;

  ul {
    display: flex;
    align-items: flex-end;
    flex-direction: column;

    margin: 0;
    padding: 0;

    list-style: none;
  }

  li {
    display: block;

    margin: 0;
    padding: 0;

    text-align: right;
  }

  a {
    display: inline-block;

    padding: 4px 0;

    color: $color_white;
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    font-weight: 700;
    text-transform: lowercase;

    &:hover {
      color: $color_main;
    }
  }
}

.mobile-menu__lang {
  padding: 19px 0;

  border-top: 1px solid $color_main;

  ul {
    display: block;

    margin: 0;
    padding: 0;

    list-style: none;

    text-align: right;

    column-count: 2;
  }

  li {
    display: block;

    margin: 0;
    padding: 0;
  }

  a {
    display: inline-block;

    padding: 8px 0;

    color: $color_white;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.22;
    text-align: right;
    text-transform: lowercase;

    &:hover {
      color: $color_main;
    }
  }
}
