.collocation__wrap {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  margin: 10px 0;
}

.collocation__item {
  margin-top: 15px;
  margin-bottom: 65px;

  &:first-child {
    margin-top: 27px;
  }

  &:last-child {
    margin: 0;
  }

  .btn--simple {
    font-size: 17px;
    font-weight: 700;
  }
}

.collocation__image-wrap {
  flex: 0 0 190px;
}

.collocation__shape-wrap {
  flex: 1 1 auto;

  background: $color_white;
}

.collocation__shape-inside {
  display: flex;
  flex-wrap: wrap;

  margin: -8px -16px;
  padding: 8px 16px;
}

.collocation__shape-item {
  width: 50%;
  padding: 8px 16px;

  @media (min-width: $mqsm) {
    width: calc( 100% / 3 );
  }
}
