.examples {
  margin: 0 0 15px 0;
}

.examples__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;

  margin: -8px;

  @media (min-width: $mqsm) {
    margin: -15px;
  }
}

.examples__item {
  width: 50%;
  padding: 8px;

  @media (min-width: $mqsm) {
    padding: 15px;
  }
}
