.logo {
  display: flex;
  align-items: center;

  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

.logo__symbol {
  width: 48px;

  color: $color_white;
}

.logo__text {
  margin: 0 0 -3px 7px;

  color: $color_white;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 900;
  letter-spacing: 1.6px;
  text-transform: lowercase;
}
