.entries {
  margin: 20px 0 40px 0;
  padding: 0;

  list-style: none;

  @media (min-width: $mqsm) {
    margin: 42px 0 70px 0;
  }

  li {
    margin: 0;
    padding: 13px 0;
  }

  a {
    color: $color_secondary;
    font-size: 18px;
    line-height: 1;

    &:hover {
      color: $color_main;
    }
  }
}
