.control {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;

  margin: 36px 0 0 0;
}

.control__count {
  padding: 0 15px;

  color: $color_secondary;
  font-size: 12px;
  font-weight: 700;
}

.control__count--break {
  order: -1;

  width: 100%;
  margin: 0 0 15px 0;
  padding: 0;

  text-align: center;

  @media (min-width: $mqmd) {
    order: initial;

    width: auto;
    margin: 0;
    padding: 0 15px;
  }
}
