.btn,
a.btn {
  display: inline-block;

  padding: 8px 14px;

  color: $color_white;
  font: inherit; // pro sladění vzhledu buttonu a odkazu
  font-size: 12px;
  font-weight: 700;
  line-height: 14px; // pro sladění výšky tlačítka
  text-decoration: none;
  text-transform: uppercase;

  border: none;
  border-radius: $radius_button;
  background-color: $color_main;

  cursor: pointer;
  transition: all 300ms ease;
  transform: translateZ( 0 );

  @media (min-width: $mqsm) {
    padding: 11px 20px;

    font-size: 14px;
  }

  &:hover,
  &:focus {
    color: $color_white;

    background-color: $color_main_dark;
  }

  .icon {
    color: $color_white;
  }
}

// sekundární tlačítko
.btn--secondary,
a.btn--secondary {
  background-color: #afafaf;

  @media (min-width: $mqsm) {
    background-color: $color_secondary;
  }

  &:hover,
  &:focus {
    background-color: $color_secondary_dark;
  }
}

// tlačítko s ikonou uprostřed
.btn--search,
a.btn--search {
  padding: 16px;

  border-radius: 10px;
  background-color: $color_secondary;

  &:hover,
  &:focus {
    background-color: $color_gray_dark;
  }

  .icon {
    color: $color_white;
  }
}

// kulaté tlačítko s ikonou uprostřed
.btn--round,
a.btn--round {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  width: 36px;
  height: 36px;
  padding: 0;

  border-radius: 100%;
  background-color: $color_main;

  @media (min-width: $mqsm) {
    width: 42px;
    height: 42px;
  }

  &:hover,
  &:focus {
    background-color: $color_main_dark;
  }

  .icon {
    width: 24px;

    color: $color_white;
  }
}

// jednoduchý textové tlačítko s šipkou vpravo
.btn--simple,
a.btn--simple {
  padding: 0 23px 0 0;

  color: $color_secondary;
  text-transform: none;

  background-color: transparent;

  &:after {
    content: '';

    position: absolute;
    top: 50%;
    right: 0;

    width: 8px;
    height: 12px;

    background: url( '../img/arrow-left.svg' ) no-repeat center;
    background-size: contain;

    transform: translate( 0, -50% );
  }

  &:hover,
  &:focus {
    color: $color_secondary;

    background-color: transparent;
  }

  .icon {
    width: 24px;

    color: $color_white;
  }

  &--lg {
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
  }
}

// tlačítko vymazat
.btn--danger,
a.btn--danger {
  background-color: $color_danger;

  &:hover,
  &:focus {
    background-color: darken( $color_danger, 10% );
  }
}

.btn__group {
  display: flex;
  flex-wrap: wrap;

  margin: -5px;

  .btn {
    margin: 5px;
  }
}

// odsazeni tlacitka pro vyber vida
a.btn {
  margin-right: 5px;
}
