.video-top {
  @media (min-width: $mqsm) {
    margin: 15px 0 47px 0;
  }
}

.video-top__wrap {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

.video-top__video {
  flex: 0 0 100%;

  @media (min-width: $mqsm) {
    flex: 1 1 65%;
  }

  @media (min-width: $mqlg) {
    flex: 1 1 315px;

    max-width: 315px;
  }
}

.video-top__content {
  flex: 0 1 auto;
  order: 3;

  margin: 28px 0 0 0;

  @media (min-width: $mqsm) {
    flex: 1 1 35%;
    order: initial;

    margin: 0;
  }

  @media (min-width: $mqlg) {
    flex: 0 0 160px;
  }
}

.video-top__buttons {
  flex: 0 0 100%;

  margin: 10px 0 0 0;
}

.video-top__text {
  font-family: 'Montserrat', sans-serif;
  font-size: 15px;
  font-weight: 700;
  line-height: 22px;
  text-transform: uppercase;
}

.video-top__icon {
  margin: 0 0 0 10px;

  cursor: pointer;

  .icon {
    width: 24px;
  }
}

.video-top__image-wrap {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  padding: 13px 27px;

  border: 3px solid $color_gray;
  background: $color_white;

  @media (min-width: $mqsm) {
    border: 0;
  }
}
