.keyboard {
  max-width: 990px;
  margin: 0 auto;
  padding: 15px;

  border-radius: 10px;
  background-color: #2b2321;

  user-select: none;

  @media (min-width: $mqmd) {
    padding: 25px;

    text-align: right;
  }

  button {
    display: inline-block;
    overflow: hidden;

    width: 45px;
    height: 45px;
    margin: 4px 2px;
    padding: 0;

    color: $color_white;
    font-size: 25px;
    font-weight: 700;
    line-height: 1;
    vertical-align: top;

    border: none;
    outline: none;
    background-color: #5e5350;
    background-repeat: no-repeat;
    background-position: center center;

    @media (min-width: $mqmd) {
      width: 58px;
      height: 58px;
    }

    &:hover,
    &:focus {
      background-color: $color_secondary;
    }
  }

  .icon {
    width: 26px;

    color: $color_white;
  }

  .keyboard__image {
    background-image: url( ../img/hamburger.svg );
  }

  .keyboard__back {
    width: 120px;

    @media (max-width: $mqmd - 1px) {
      display: none;
    }
  }

  .keyboard__enter {
    width: 95px;

    @media (max-width: $mqmd - 1px) {
      display: none;
    }
  }

  .keyboard__special {
    background-color: #574d4c;
  }

  .keyboard__space {
    width: 30%;

    @media (min-width: $mqmd) {
      width: 100%;
      max-width: 520px;
    }
  }
}

// zalomení
.keyboard__hide {
  @media (max-width: $mqmd - 1px) {
    display: none;
  }
}
