.detail {
  @media (min-width: $mqmd) {
    background: $color_white url( ../img/section-half.png ) center top repeat-y;
  }
}

.detail__head {
  position: relative;

  padding: 30px 15px;

  @media (min-width: $mqsm) {
    max-width: 50%;
    padding: 30px 10px;
  }

  @media (min-width: $mqmd) {
    padding: 33px 60px 70px 10px;
  }
}

.detail__badge {
  display: inline-block;

  padding: 7px 10px;

  color: $color_secondary;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;

  background: $color_main;
}

.detail__badge--top {
  position: absolute;
  top: 0;
  right: 10px;
}

.detail__block {
  position: relative;

  padding: 0 15px;

  @media (min-width: $mqmd) {
    padding: 30px 60px 70px 10px;
  }

  h3 {
    color: $color_main;
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
  }
}

.detail__block--wide {
  padding-right: 0;
}

.detail__block--line {
  padding-top: 30px;

  &:before {
    content: '';

    position: absolute;
    top: 0;

    display: block;

    width: 90%;
    height: 1px;

    background: url( '../img/hr.svg' ) repeat-x left;
    background-size: cover;
  }
}
